import React, { useState, useEffect } from "react";
import { Grid, IconButton } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import { Edit } from '@material-ui/icons'

// styles
import useStyles from "./styles";

// components
import PageTitle from "../../components/PageTitle/PageTitle";
import { bindActionCreators } from "redux";
import { useHistory } from "react-router-dom";
import { useSelector, connect } from "react-redux";
import fetchClientView from "../../services/clientview/ClientViewService";
import { toast, ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import { SERVER_URL } from '../../common/config';

function ClientViewPage(props) {
  let history = useHistory();
  const [dataSource, setDataSource] = useState([]);
  const clientViewData = useSelector(state => state.clientview);

  useEffect(() => {
    console.log(clientViewData)
    props.fetchClientView()
    setDataSource(clientViewData.clientview);
  }, [])

  //Show notification
  const notify = (message) => toast(message);

  const columns = [
    {
      name: "client_id",
      label: "ID",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "client_entity_name",
      label: "Entity Name",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "client_owner_name",
      label: "Owner Name",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "address",
      label: "Address",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "location",
      label: "Location",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "phone_number",
      label: "Phone Number",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "client_id",
      label: "Action",
      options: {
        filter: false,
        sort: false,
        customBodyRender: (value) => {
          // console.log("==================>", value, tableMeta, updateValue)
          return (
            <>
              <IconButton
                onClick={(e) => {
                  actionEdit(e, value)
                }}
              >
                <Edit style={{ fontSize: '18' }} />
              </IconButton>
            </>
          );
        }
      },
    },
  ];



  const actionEdit = (e, i) => {
    history.push("/app/clientview/" + i + "/edit");
  }

  const options = {
    filterType: 'dropdown',
    pagination: true,
    print: false,
    download: false,
    filter: true,
    responsive: 'scroll',
    fixedHeader: false, elevation: 0,
    rowsPerPageOptions: [5, 10, 20],
    resizableColumns: false,
    onRowsDelete: (rowsDeleted) => {

      const delete_id = []
      rowsDeleted.data.map((data) => {
        const newDeleteId = clientViewData.client[data.dataIndex].client_id
        delete_id.push(newDeleteId)
      })
      console.log("deleting Ids===> ", delete_id)
      delete_id.map((id) => {
        // row delete api call
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            client_id: id
          })
        };
        fetch(`${SERVER_URL}deleteClient`, requestOptions)
          .then(async response => {
            const data = await response.json();
            console.log("Response Data=============>", data)
            // check for error response
            if (!response.ok) {
              // get error message from body or default to response status
              const error = (data && data.message) || response.status;
              return Promise.reject(error);
            }
            return
          })
          .catch(error => {
            notify('Something went wrong!\n' + error)
            console.error('There was an error!', error);
          });
      })
    },
    onTableChange: (action, tableState) => {
      console.log(action, tableState);
      let tmp = [];
      tableState.data.map((item) => {
        tmp.push(item.data);
      });
      console.log(tmp);
    }
    
  };



  return (
    <>
      <PageTitle title="Clients" button={["Add New"]} data={dataSource} category="clientview" history={history} />
      <Grid container spacing={4}>
        <Grid item xs={12} md={12}>
          <MUIDataTable
            title={"Clients"}
            data={clientViewData.clientview}
            // data={dataSource}
            columns={columns}
            options={options}
          />
        </Grid>
      </Grid>
    </>
  );
}


const mapStateToProps = state => ({
  clientview: state.clientview
})

const mapDispatchToProps = dispatch => bindActionCreators({
  fetchClientView: fetchClientView
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ClientViewPage);
